<template>
        <scroll class="scroll-outside" :scrollBoo="outScrollBoo" :scroll-config="{
                    scrollbar: {
                        fade: true,
                        interactive: false
                    },
                    click: true,
                    mouseWheel: true,
                    pullUpLoad: {
                        threshold: 50
                    },
                    pullDownRefresh: {
                        threshold: 50,
                        stop: 20
                    }
                }">
            <ul class="hot-articles-ul">
                <li class="article-item-li" v-for="(c,i) in activityArticleListData" v-bind:key="i">
                    <div class="title" @click="getArticleData(i,$event)">
                        <h5 class="h5">{{c.title}}</h5>
                        <p class="author-p">作者：{{c.author}}</p>
                    </div>
                    <scroll class="scroll-inside" :scrollConfig="{
                                scrollX: true,
                                scrollY: false,
                                mouseWheel: true
                            }" v-on:mouseenter.native="disableOutScroll()" v-on:mouseleave.native="enableOutScroll()">
                        <ul class="img-ul" ref="preventEl" v-on:touchstart='preventSwiper()' v-on:touchend='recoverSwiper()'>
                            <li class="img-li" v-for="(src,i) in c.image" :key="i" >
                                <img :src="src" alt="文章配图" width="120" height="100" class="img">
                            </li>
                        </ul>
                    </scroll>
                </li>
            <div class="empty-div"></div>
            </ul>
        </scroll>
</template>

<script>
import openArticle from '../assets/js/openArticle.js';
import scroll from "../components/scroll.vue";
//导入文章数据（实际上是需要异步请求）
import userStorys from '../../data/userStorys.js'
export default {
  props: ["activityArticleListData"],
  data() {
    return {
      outScrollBoo: true
    };
  },
  components: {
    scroll
  },
  methods: {
    disableOutScroll() {
      this.outScrollBoo = false;
    },
    enableOutScroll() {
      this.outScrollBoo = true;
    },
    preventSwiper() {
        let preventEl = this.$refs.preventEl;
      window.preventSwiperVue.$emit('preventSwiper',preventEl, true);
    },
    recoverSwiper() {
        let preventEl = this.$refs.preventEl;
      window.preventSwiperVue.$emit('preventSwiper',preventEl, false);
    },
    getArticleData(i,e){ 
        openArticle(userStorys[i])
    }
  }
};
</script>

<style lang="stylus" scoped>
.scroll-outside {
    padding: 10px 0 0;
    border: rgba(0, 0, 0, 0.3) 1px solid;
    border-radius: 5px;
    flex-grow: 1;

    .hot-articles-ul {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        width: 100%;

        .article-item-li {
            display: flex;
            justify-content: space-between;
            height: 112px;
            width: 90%;
            border: rgba(0, 0, 0, 0.3) 1px solid;
            margin-bottom: 5px;
            padding: 5px;
            cursor: pointer;

            &:hover {
                border: rgba(255, 255, 255, 0.5) 1px solid;
            }

            .title {
                width: 50%;
                height: 100%;
                position: relative;

                &:hover .h5 {
                    text-decoration: underline;
                }

                .h5 {
                    display: -webkit-box !important;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    word-break: break-all;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 2;
                    font-size: 14px;
                }

                .author-p {
                    position: absolute;
                    bottom: 20px;
                    font-size: 12px;
                }
            }

            .scroll-inside {
                width: 50%;
                height: 100%;
                overflow: hidden;

                .img-ul {
                    height: 100%;
                    display: inline-flex;
                    cursor: default;

                    .img-li {
                        height: 100%;
                        list-style: none;
                        margin-right: 5px;
                    }
                }
            }
        }

        .empty-div {
            height: 50px;
        }
    }
}
</style>



