<template>
  <header id="header">
    <div class="logo">
      <img src="../assets/logo.png" alt="租我么logo" height="50" width="50">
    </div>
    <div class="download-btn">
      <span class="download-btn-span" id="download-btn-span">点击安装App</span>
    </div>
    <div class="grade-btn" id="grade-btn">
      <span class="grade-btn-span">给应用打分</span>
    </div>
    <div class="expand-btn" id="expand-btn">
      <span class="expand-btn-span">帮助租我么壮大起来</span>
    </div>
    <div class="fold-btn">
      <span class="icon-chevron-down" id="fold-btn"></span>
    </div>
  </header>
</template>

<script>
export default {
  mounted() {
    $("#fold-btn").on("click", function() {
      let expandBtn = $("#expand-btn");
      let gradeBtn = $("#grade-btn");
      let dlBtnSpan = $("#download-btn-span");
      let thisClassName = this.className;
      expandBtn.toggleClass("move-left0");
      gradeBtn.toggleClass("move-left42");
      if (thisClassName === "icon-chevron-down") {
        this.className = "icon-chevron-up";
      } else if (thisClassName === "icon-chevron-up") {
        this.className = "icon-chevron-down";
      }
    });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
#header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 50px;
  border-bottom: solid 1px #7569104d;
  overflow: hidden;

  .move-left0 {
    left: 0 !important;
  }

  .move-left42 {
    left: 0 !important;
  }

  .logo, .download-btn, .fold-btn {
    flex-grow: 1;
    height: 100%;
    line-height: 50px;
  }

  @media only screen and (min-width: 769px) {
    .grade-btn, .expand-btn {
      flex-grow: 1;
      height: 100%;
      line-height: 50px;
    }
  }

  .logo, .download-btn, .grade-btn, .expand-btn, .fold-btn {
    span {
      border-radius: 5px;
      border: 1px solid rgba(0, 0, 0, 0.3);
      cursor: pointer;
    }
  }

  .logo, .download-btn, .grade-btn, .expand-btn {
    span {
      &:hover {
        border: rgba(255, 255, 255, 0.5) 1px solid;
        color: rgba(255, 255, 255, 1);
      }
    }
  }

  .logo {
    flex-grow: 1;
    width: 61px;
    text-align: center;

    img {
      vertical-align: top;
    }
  }

  .download-btn {
    flex-grow: 10;
    text-align: center;

    .download-btn-span {
      padding: 5px 100px;

      @media only screen and (max-width: 375px) {
        padding: 5px 30px;
      }
    }
  }

  .expand-btn {
    @media only screen and (min-width: 769px) {
      flex-grow: 1;
      text-align: center;
      transition: all 0.3s;
      font-size: 14px;
      color: #000;
    }

    @media only screen and (max-width: 768px) {
      position: fixed;
      left: 100%;
      top: 49px;
      height: 40px;
      width: 100%;
      z-index: 1000;
      transform: translate3d(0, 0, 0);
      transition: all 0.3s;
    }

    .expand-btn-span {
      padding: 5px 10px;

      @media only screen and (max-width: 768px) {
        display: inline-block;
        width: 100%;
        background-color: #F0BD00;
      }
    }
  }

  .grade-btn {
    @media only screen and (min-width: 769px) {
      flex-grow: 1;
      text-align: right;
      transition: all 0.3s;
      z-index: 1000;
      font-size: 14px;
      color: #000;
    }

    @media only screen and (max-width: 768px) {
      position: fixed;
      left: 100%;
      top: 83px;
      height: 40px;
      width: 100%;
      z-index: 1000;
      transform: translate3d(0, 0, 0);
      transition: all 0.4s;
    }

    .grade-btn-span {
      padding: 5px 10px;

      @media only screen and (max-width: 768px) {
        display: inline-block;
        width: 100%;
        background-color: #F0BD00;
      }
    }
  }

  .fold-btn {
    flex-grow: 1;
    width: 61px;
    text-align: center;

    @media only screen and (min-width: 769px) {
      display: none !important;
    }

    @media only screen and (max-width: 768px) {
      display: block;
    }

    .icon-chevron-down, .icon-chevron-up {
      @media only screen and (max-width: 768px) {
        padding: 10px;
        border: none;
      }
    }
  }
}
</style>



