<template>
    <scroll class="scroll-container" :scroll-config="{
            scrollbar: {
                fade: true,
                interactive: false
            },
            click: true,
            mouseWheel: true,
            pullUpLoad: {
                threshold: 50
            },
            pullDownRefresh: {
                threshold: 50,
                stop: 20
            }
        }">
        <ul class="video-user-list-ul">
            <li class="video-user-list-li" v-for="(c,i) in videoListData" :key="i">
                <img :src="c.imgSrc" class="video-coverimg" alt="视频封面图" width="100%" height="500px">
                <span class="icon-play-circle video-paly-btn" @click="playvideo(i,$event)"></span>
                <p v-show="false" class="video-title">
                    {{ c.title }}
                </p>
            </li>
            <div class="empty-div"></div>
        </ul>
        <div class="video-play-container" ref="videoPlayContainer" @touchmove.stop='preventDefault()'  v-for="(c,i) in videoListData" :key="i">
            <video 
                class="video-tag" ref="videoTag"
                playsinline
                webkit-playsinline
                x-webkit-airplay="true"
                x5-video-player-type="h5"
                x5-video-player-fullscreen="true">
                <source v-for="(citem,index) in c.videoSrc" :key="index" :src="citem">
                您的浏览器不支持视频标签
            </video>
            <span class="shutdown-video-btn icon-remove" @click="shutdownvideo(i,$event)"></span>
        </div>
    </scroll>
</template>

<script>
import scroll from "../components/scroll.vue";
export default {
  data() {
    return {};
  },
  props: {
    videoListData: Array,
    videoPosition: {
      type: String,
      default: "0"
    }
  },
  components: {
    scroll
  },
  methods: {
    playvideo(i, e) {
      //打开video播放组件,并开始播放视频
      this.$refs.videoPlayContainer[i].style.transform = "scale(1)";
    //   this.$refs.videoTag[i].play();
    },
    shutdownvideo(i, e) {
      //关闭video播放组件,并暂停播放视频
      this.$refs.videoPlayContainer[i].style.transform = "scale(0)";
    //   this.$refs.videoTag[i].pause();
    },
    preventDefault(){ return }
  },
  mounted() {
    if (window.innerWidth <= 768) {
      this.$refs.videoPlayContainer.forEach((c, i) => {
        c.style.left = this.videoPosition;
      });
    }
  }
};
</script>

<style lang="stylus" scoped>
.scroll-container {
    position: relative;
    margin-bottom: 5px;
    flex-grow: 1;
    overflow: hidden;
    padding: 10px 0 0;
    width: 100%;

    .video-user-list-ul {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;

        .video-user-list-li {
            position: relative;
            list-style: none;
            width: 95%;

            .video-paly-btn {
                position: absolute;
                left: 50%;
                top: 50%;
                color: #ffffff;
                font-size: 50px;
                transform: translate(-50%, -50%);
                cursor: pointer;

                &:hover {
                    color: #fdda02;
                }
            }

            .video-title {
                position: absolute;
                left: 0;
                bottom: 20px;
            }
        }

        .empty-div {
            height: 50px;
        }
    }

    .video-play-container {
        position: fixed;
        left: 0;
        top: 0;
        z-index: 1000;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.8);
        transition: all 0.3s;
        transform: scale(0);

        @media only screen and (max-width: 768px) {
            width: 25%;
        }

        .video-tag {
            width: 100%;
            height: 100%;
        }

        .shutdown-video-btn {
            position: absolute;
            right: 0px;
            top: 0px;
            padding: 3px;
            font-size: 30px;
            cursor: pointer;
            color: #fff;

            &:hover {
                color: #fdda02;
            }
        }
    }
}
</style>



