<template>
    <span class="boy-girl-icon" :class="this.classObj3" v-bind:style="{margin:margin}">
        <span class="icon-search" :class="this.classObj"></span>
        <span :class="this.classObj2"></span>
    </span>
</template>
<script>
export default {
  data() {
    return {
      classObj: {
        "icon-search-boy": !this.girl,
        "icon-search-girl": this.girl
      },
      classObj3: {
        "boy-icon": !this.girl,
        "girl-icon": this.girl
      },
      classObj2: {
        "icon-chevron-right": !this.girl,
        "icon-plus": this.girl
      }
    };
  },
  props: {
    margin: {
      default: "0 0 0 0"
    },
    girl: {
      default: false
    }
  },
  created: function() {
    this.$nextTick(function() {});
  }
};
</script>
<style lang="stylus" scoped>
.boy-girl-icon {
    display: inline-block;
    font-size: 10px;

    .icon-search-boy {
        display: inline-block;
        transform: rotateZ(-90deg);
    }

    .icon-chevron-right {
        display: inline-block;
        transform-origin: left;
        transform: translate(-4px, -3px) rotateZ(-45deg);
    }

    .icon-search-girl {
        display: inline-block;
        transform: rotateZ(45deg);
    }

    .icon-plus {
        display: inline-block;
        transform: translate(-14px, 7px);
    }
}

.girl-icon {
    transform: translateY(-2px);
}

.boy-icon {
    transform: translateY(2px);
}
</style>



