export default function(eventType,father,childSelector,fn){
    let jqFather;
    if (typeof father === 'string') {
        jqFather = $(father);
    } else if(typeof father === 'object') {
        jqFather = $(father);
    } else{
        return;
    }
    jqFather.on(eventType,function(e){
        let target = e.target;
        let selectorId = childSelector.slice(0,1);
        let selectorName = childSelector.slice(1);
        let targetClass = target.className;
        let targetId = target.id;
        if (selectorId === '.' && targetClass.includes(selectorName)) {
            fn();
        } else if (selectorId === '#' && targetId.includes(selectorName)) {
            fn();
        }
    });
};



