<template>
  <div id="container-body" ref="body">
    <div-header></div-header>
    <div-main></div-main>
    <footer></footer>
    <!-- <div class="view-div">
      <router-view></router-view>
    </div> -->
  </div>
</template>

<script>
import divHeader from "./components/divHeader";
import divMain from "./components/divMain";
export default {
  components: {
    divHeader,
    divMain
  },
  mounted: function() {
    this.$nextTick(function() {
      $("#container-body").height(window.innerHeight);
    });
  }
};
</script>

<style lang="stylus" scoped>
#container-body {
  position: relative;
  background: radial-gradient(#fdda02, #e7a700);
  overflow: hidden;
}
</style>



