export default function () {
    setTimeout(function () {
        $("#view-div").css({
            left: "0"
        });

        if (window.innerWidth > 768) {
            setTimeout(function () {
                //点击事件发生时，不知是谁更改了swiper的宽度，所以需要改回来
                $(".swiper-slide").width(window.innerWidth * 0.22);
            }, 0);
        }
    }, 50);
}


