<template>
    <scroll :scrollBoo="outScrollBoo" class="list-container" :scroll-config="{
            scrollbar: {
                fade: true,
                interactive: false
            },
            click: true,
            mouseWheel: true,
            pullUpLoad: {
                threshold: 50
            },
            pullDownRefresh: {
                threshold: 50,
                stop: 20
            }
        }">
        <ul class="ul">
            <li v-for="(item, index) in data" :key="index" class="li">
                <div class="article-synopsis" @click="getArticleData(index,$event)">
                    <h4 class="h4">
                        <span>{{ item.title }}</span>
                    </h4>
                    <p>
                        <span class="author">作者：{{ item.author }}</span>
                        <span class="meme-number"> | 么么号：{{item.memeNumber}}</span>
                    </p>
                    <p class="dateTime">
                        <span>{{item.createDate}} {{item.createTime}}</span>
                    </p>
                    <p class="description">{{item.description}}</p>
                </div>
                <scroll class="imgs-container" :scrollConfig="{
                        scrollX: true,
                        scrollY: false,
                        mouseWheel: true
                    }" v-on:mouseenter.native="disableOutScroll()" v-on:mouseleave.native="enableOutScroll()">
                    <ul class="scroll-x" id="scroll-x">
                        <li class="img-wrapper" v-for="(src,i) in item.image" :key="i">
                            <img :src="src" alt="文章配图" width="200" height="150">
                        </li>
                    </ul>
                </scroll>
            </li>
            <div class="empty-div"></div>
        </ul>
    </scroll>
</template>

<script>
import openArticle from '../assets/js/openArticle.js';
import scroll from "../components/scroll.vue";
//导入文章数据（实际上是需要异步请求）
import userStorys from '../../data/userStorys.js'
export default {
  props: ["data"],
  data() {
    return {
      outScrollBoo: true
    };
  },
  components: {
    scroll
  },
  methods: {
    disableOutScroll() {
      this.outScrollBoo = false;
    },
    enableOutScroll() {
      this.outScrollBoo = true;
    },
    getArticleData(i,e){ 
        openArticle(userStorys[i])
    }
  }
};
</script>
<style lang="stylus" scoped>
.list-container {
    position: relative;
    margin-top: 5px;
    height: 100%;
    overflow: hidden;

    .ul {
        @media only screen and (max-width: 768px) {
            width: 90%;
        }

        @media only screen and (min-width: 769px) and (max-width: 1024px) {
            width: 90%;
        }

        @media only screen and (min-width: 1025px) {
            width: 85%;
        }

        margin: 0 auto;

        .li {
            width: 100%;
            overflow: hidden;
            display: flex;
            margin-top: 5px;
            cursor: default;

            @media only screen and (max-width: 768px) {
                flex-direction: column;
            }

            list-style: none;
            border: solid #eee 1px;
            padding: 5px;

            .article-synopsis {
                @media only screen and (min-width: 769px) {
                    width: 50%;
                    cursor: pointer;
                }

                flex-grow: 1;
                padding: 5px;
                font-size: 14px;
                overflow: hidden;

                .h4 {
                    display: -webkit-box !important;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    word-break: break-all;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 2;
                    font-size: 16px;

                    &:hover {
                        text-decoration: underline;
                    }
                }

                .author {
                    color: #999;
                }

                .meme-number {
                    color: #999;
                }

                .dateTime {
                    color: #aaa;
                }

                .description {
                    text-indent: 2em;
                    display: -webkit-box !important;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    word-break: break-all;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 3;
                    color: #888;
                }
            }

            .imgs-container {
                @media only screen and (min-width: 769px) {
                    width: 50%;
                }

                box-sizing: content-box;
                overflow: hidden;

                .scroll-x {
                    display: inline-flex;

                    .img-wrapper {
                        list-style: none;
                        margin-right: 5px;
                    }
                }
            }
        }

        .empty-div {
            height: 50px;
        }
    }
}
</style>



