import $ from 'jquery'
import Vue from 'vue'
import App from './App'
import './assets/css/reset.css'
import './assets/css/font-awesome.min.css'
import './assets/css/swiper-4.1.0.min.css'
Vue.config.productionTip = false
window.refreshBScroll = [];

/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App></App>',
  components: {
    App
  }
})



