<template>
  <div class="chart-div" ref="chartDiv" @scroll="scrollToOne()">
    <div>
      <canvas id="canvas" ref='canvas'></canvas>
    </div>
    <div class="text-description" ref='textDescription'>
      <div class="scroll-y">
        <p>总用户人数：{{data.amount}}</p>
        <p>男生：{{data.boys}}</p>
        <p>女生：{{data.girls}}</p>
        <!--真实应用场景下，不需要下面的p标签和其内容-->
        <hr />
        <p style="font-size:12px;">真实应用场景下，不需要下面内容，仅供技术测试用</p>
        <p>输入男生人数：<input type="number" ref='boys' /></p>
        <p>输入男生Before90人数：<input type="number" ref='boysBefore90' /></p>
        <p>输入男生After90人数：<input type="number" ref='boysAfter90' /></p>
        <p>输入男生After00人数：<input type="number" ref='boysAfter00' /></p>

        <p>输入女生人数：<input type="number" ref='girls' /></p>
        <p>输入女生Before90人数：<input type="number" ref='girlsBefore90' /></p>
        <p>输入女生After90人数：<input type="number" ref='girlsAfter90' /></p>
        <p>输入女生After00人数：<input type="number" ref='girlsAfter00' /></p>
        <p><a ref='btn' class="a-btn">确定</a></p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["data"],
  data() {
    return {
      boys: 10,
      girls: 0,
      boysBefore90: 0,
      boysAfter90: 0,
      boysAfter00: 0,
      girlsBefore90: 0,
      girlsAfter90: 0,
      girlsAfter00: 0
    };
  },
  mounted() {
    let canvas = this.$refs.canvas;
    let chartDiv = this.$refs.chartDiv;
    let btn = this.$refs.btn;

    //真实应用场景下，不需要下面这些变量
    let boys = this.$refs.boys;
    let boysBefore90 = this.$refs.boysBefore90;
    let boysAfter90 = this.$refs.boysAfter90;
    let boysAfter00 = this.$refs.boysAfter00;

    let girls = this.$refs.girls;
    let girlsBefore90 = this.$refs.girlsBefore90;
    let girlsAfter90 = this.$refs.girlsAfter90;
    let girlsAfter00 = this.$refs.girlsAfter00;
    //以上

    let boundingRect = chartDiv.getBoundingClientRect();
    let parentWidth = boundingRect.width;
    let parentHeight = boundingRect.height;

    if (window.innerWidth > window.innerHeight) {
      canvas.width = parentWidth * 0.5;
      canvas.height = parentHeight;
    } else if (window.innerWidth < window.innerHeight) {
      canvas.width = parentWidth;
      canvas.height = parentHeight * 0.5;
    }

    let ctx = canvas.getContext("2d");

    //确定圆心
    let centerX = canvas.width * 0.5;
    let centerY = canvas.height * 0.5;

    let rootItems = [
      {
        name: "boys",
        value: 10000000,
        children: [
          {
            name: "before90",
            value: 5000000
          },
          {
            name: "after90",
            value: 3000000
          },
          {
            name: "after00",
            value: 2000000
          }
        ]
      },
      {
        name: "girls",
        value: 12000000,
        children: [
          {
            name: "before90",
            value: 5000000
          },
          {
            name: "after90",
            value: 3000000
          },
          {
            name: "after00",
            value: 4000000
          }
        ]
      }
    ];

    btn.addEventListener(
      //真实应用场景下，不存在这个功能
      "click",
      function(e) {
        let boysValue = boys.value;
        let girlsValue = girls.value;

        if (
          !boysValue ||
          !girlsValue ||
          !boysBefore90.value ||
          !boysAfter90.value ||
          !boysAfter00.value ||
          !girlsBefore90.value ||
          !girlsAfter90.value ||
          !girlsAfter00.value
        ) {
          alert("请填写数据");
          return;
        }

        rootItems[0].value = parseInt(boysValue);
        rootItems[0].children[0].value = parseInt(boysBefore90.value);
        rootItems[0].children[1].value = parseInt(boysAfter90.value);
        rootItems[0].children[2].value = parseInt(boysAfter00.value);

        rootItems[1].value = parseInt(girlsValue);
        rootItems[1].children[0].value = parseInt(girlsBefore90.value);
        rootItems[1].children[1].value = parseInt(girlsAfter90.value);
        rootItems[1].children[2].value = parseInt(girlsAfter00.value);

        ctx.clearRect(0, 0, canvas.width, canvas.height);

        strokeSunburstFn({
          rootItems: rootItems,
          startDegree: -90
        });
      },
      false
    );

    strokeSunburstFn({
      rootItems: rootItems,
      startDegree: -90
    });

    function strokeSunburstFn(config) {
      //计算各元素的位置数据，上限两层

      //计算根元素
      let amount = 0; //计算总数
      let rootItemsValue = []; //按顺序保存各项目的值 和 名称
      let childrenArr = []; //按顺序保存各项目的子元素
      config.rootItems.forEach(function(c, i) {
        amount = amount + c.value;
        rootItemsValue.push({
          name: c.name,
          value: c.value
        });
        if (Array.isArray(c.children)) {
          childrenArr.push(c.children);
        } else {
          childrenArr.push([]);
        }
      });

      let rootItemsDegArr = rootItemsValue.map(function(c, i) {
        //根元素各项目的角度
        return {
          name: c.name,
          degree: c.value / amount * 360
        };
      });
      let rootItemsStartAndEnd = []; //根元素的起始角度和终止角度存放数组
      let startDeg = config.startDegree;
      let endDeg;

      for (let i = 0; i < rootItemsDegArr.length; i++) {
        endDeg = rootItemsDegArr[i].degree + startDeg;
        rootItemsStartAndEnd.push({
          name: rootItemsDegArr[i].name,
          degree: rootItemsDegArr[i].degree,
          startDeg,
          endDeg
        });
        startDeg = endDeg;
      }

      //计算子元素
      let arr = [];
      let degreeArr = [];
      childrenArr.forEach(function(c, i) {
        //遍历children数组
        let childStartDeg = rootItemsStartAndEnd[i].startDeg; //每个child数组都有一个起始位置
        let amount = config.rootItems[i].value; //每个child数组都有一个总数

        let childItemArr = []; //成员包含每个child的名字 起始角度 终止角度 角度
        let childrenAmountDegree = rootItemsDegArr[i].degree;
        let startDeg = childStartDeg;
        let endDeg;

        for (let x = 0; x < c.length; x++) {
          let degree = c[x].value / amount * childrenAmountDegree; //通过总数算出child数组每个成员所占的角度
          degreeArr.push(degree);
          endDeg = startDeg + degree;
          childItemArr.push({
            name: c[x].name,
            startDeg,
            endDeg,
            degree
          });
          startDeg = endDeg;
        }

        arr.push(childItemArr);
      });

      //设置颜色
      let colorArr0 = ["#4484F4", "#f6043e", "#FBBB0A"];
      let colorArr1 = ["#F0652A", "#CC9E00", "#6b58ff"];
      let colorArr2 = ["#F34F2B", "#B5e022", "#E94538"];
      let colorArr = [colorArr0, colorArr1, colorArr2];
      //绘制
      let insideR;
      let outsideR;
      if (window.innerWidth >= 768) {
        insideR = centerY * 0.3;
        outsideR = centerY * 0.5;
      } else if (window.innerWidth < 768) {
        insideR = centerX * 0.3;
        outsideR = centerX * 0.5;
      }
      for (let j = 0; j < arr.length; j++) {
        strokeArc(arr[j], colorArr[j], outsideR);
      }
      strokeArc(rootItemsStartAndEnd, colorArr2, insideR);

      //绘制文字标识
      //分别计算内层和外层元素的文字坐标，并保存在数组中
      let outsideWordsArr = [];
      let insideWordsArr = [];

      function getWordPosition(c, saveArr, R) {
        for (let j = 0; j < c.length; j++) {
          let centerDegree = c[j].endDeg - c[j].degree * 0.5;
          if (centerDegree < 0) {
            centerDegree = centerDegree + 360;
          }
          switch (centerDegree) {
            case 0:
            case 360:
              saveArr.push({
                name: c[j].name,
                x: centerX + R,
                y: centerY
              });
              break;
            case 90:
              saveArr.push({
                name: c[j].name,
                x: centerX,
                y: centerY + R
              });
              break;
            case 180:
              saveArr.push({
                name: c[j].name,
                x: centerX - R,
                y: centerY
              });
              break;
            case 270:
              saveArr.push({
                name: c[j].name,
                x: centerX,
                y: centerY - R
              });
              break;

            default:
              break;
          }

          if (centerDegree > 270) {
            let xDeg = centerDegree - 270;
            let yDeg = 90 - xDeg;
            let xLine = R * Math.sin(xDeg * Math.PI / 180);
            let yLine = R * Math.sin(yDeg * Math.PI / 180);
            saveArr.push({
              name: c[j].name,
              x: centerX + xLine,
              y: centerY - yLine
            });
          } else if (centerDegree > 180) {
            let yDeg = centerDegree - 180;
            let xDeg = 90 - yDeg;
            let xLine = R * Math.sin(xDeg * Math.PI / 180);
            let yLine = R * Math.sin(yDeg * Math.PI / 180);
            saveArr.push({
              name: c[j].name,
              x: centerX - xLine,
              y: centerY - yLine
            });
          } else if (centerDegree > 90) {
            let xDeg = centerDegree - 90;
            let yDeg = 90 - xDeg;
            let xLine = R * Math.sin(xDeg * Math.PI / 180);
            let yLine = R * Math.sin(yDeg * Math.PI / 180);
            saveArr.push({
              name: c[j].name,
              x: centerX - xLine,
              y: centerY + yLine
            });
          } else if (centerDegree > 0) {
            let yDeg = centerDegree - 0;
            let xDeg = 90 - yDeg;
            let xLine = R * Math.sin(xDeg * Math.PI / 180);
            let yLine = R * Math.sin(yDeg * Math.PI / 180);
            saveArr.push({
              name: c[j].name,
              x: centerX + xLine,
              y: centerY + yLine
            });
          }
        }
      }

      getWordPosition(rootItemsStartAndEnd, insideWordsArr, insideR); //内层
      arr.forEach(function(c, i) {
        //外层
        getWordPosition(c, outsideWordsArr, outsideR);
      });

      //绘制外层
      outsideWordsArr.forEach(function(c, i) {
        let textX = c.x >= centerX ? c.x : c.x - ctx.measureText(c.name).width;
        ctx.beginPath();
        ctx.font = "14px Georgia";
        ctx.fillStyle = "#000";
        ctx.lineWidth = 1;
        ctx.fillText(c.name, textX, c.y);
      });
      //绘制内层
      insideWordsArr.forEach(function(c, i) {
        let textX = c.x > centerX ? c.x - ctx.measureText(c.name).width : c.x;
        ctx.beginPath();
        ctx.font = "14px Georgia";
        ctx.fillStyle = "#000";
        ctx.lineWidth = 1;
        ctx.fillText(c.name, textX, c.y);
      });
    }

    function strokeArc(arr, colorArr, r) {
      //定义绘制扇形的function
      for (let i = 0; i < arr.length; i++) {
        ctx.beginPath();
        ctx.moveTo(centerX, centerY);
        ctx.arc(
          centerX,
          centerY,
          r,
          arr[i].startDeg * Math.PI / 180,
          arr[i].endDeg * Math.PI / 180
        );
        ctx.fillStyle = colorArr[i];
        ctx.strokeStyle = "#eee";
        ctx.lineWidth = 5;
        ctx.closePath();
        ctx.stroke();
        ctx.fill();
      }
    }
  },
  methods: {
    scrollToOne() {//解决触发个别浏览器下拉刷新的问题
      let scrollEl = this.$refs.chartDiv;
      let sct = scrollEl.scrollTop;
      if (sct < 2 && sct > -2) {
        scrollEl.scrollTop = 1;
      }
    }
  }
};
</script>

<style lang="stylus" scoped>
.chart-div {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  @media only screen and (min-width: 1025px) {
    display: flex;
  }

  .text-description {
    @media only screen and (min-width: 1025px) {
      padding-top: 30px;
    }

    @media only screen and (max-width: 1024px) {
      padding-left: 30px;
    }

    p {
      margin: 2px 0;

      input {
        width: 100px;
      }
    }

    .a-btn {
      display: block;
      width: 50px;
      height: 30px;
      background-color: red;
      cursor: pointer;
      text-align: center;
      line-height: 30px;
      font-size: 14px;
    }
  }
}
</style>



