import renderArticle from './renderArticle.js'
import newBscroll from './newBscroll.js'
export default function (articleData) {
    let articleViewDiv = $('#article-view-div');
    articleViewDiv.html(renderArticle(articleData, articleViewDiv[0].attributes[0].name));

    setTimeout(function () {
        $('#article-wrapper').css({
            transform: 'translate3d(0,0,0)',
            left: '-100%'
        });
        window.bScroll = newBscroll('.article-scroll-wrapper', {
            scrollbar: {
                fade: true,
                interactive: false
            },
            click: true,
            mouseWheel: true
        })
    }, 50)
};


