<template>
    <div class="user-story">
        <p class="story-title" @click="toggleSlide()">
            <span v-show="windowWidth < 769">点击查看</span>租我么线下活动
            <span class="icon-reorder" v-show="windowWidth < 769"></span>
        </p>
        <div class="userClassify">
            <div class="story-class story-boy fold-item-oa" @click="emitEvent({ componentName:'activityList',dataName:'skillExchange', title:'活动列表'},$event)">
                技能交流活动
                <span class="icon icon-wrench"></span>
            </div>
            <div class="story-class story-girl fold-item-oa" @click="emitEvent({ componentName:'activityList',dataName:'encounterBoys', title:'活动列表'},$event)">
                遇到男神活动
                <span class="icon icon-user"></span>
            </div>
            <div class="story-class story-renter fold-item-oa" @click="emitEvent({ componentName:'activityList',dataName:'encounterGirls', title:'活动列表'},$event)">
                遇到女神活动
                <span class="icon icon-user"></span>
            </div>
        </div>
        <activityArticleList :activityArticleListData='activityArticleListData'></activityArticleList>
    </div>
</template>

<script>
import viewDivSlide from "../assets/js/viewdivslide.js";
import activityArticleList from "./activityArticleList";
export default {
  data() {
    return {
      windowWidth: window.innerWidth
    };
  },
  props: ["activityArticleListData"],
  components: {
    activityArticleList
  },
  methods: {
    emitEvent(message, e) {
      this.$emit("getData", message);

      viewDivSlide();
    },
    toggleSlide() {
      $(".fold-item-oa").toggleClass("toggle-slide");
    }
  }
};
</script>

<style lang="stylus" scoped>
.user-story {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    padding-top: 10px;
    overflow: hidden;

    .story-title {
        text-align: center;
        height: 30px;
        line-height: 30px;
        user-select: none;

        @media only screen and (max-width: 768px) {
            border: rgba(0, 0, 0, 0.3) 1px solid;
            margin-bottom: 10px;
        }
    }

    .userClassify {
        width: 100%;

        @media only screen and (max-width: 768px) {
            position: relative;

            .fold-item-oa {
                transform: translate3d(0,0,0);
                position: absolute;
                left: 100%;
                background-color: #EDB600;
                z-index: 100;
            }

            .toggle-slide {
                left: 0 !important;
            }

            .story-boy {
                top: -10px;
                transition: 0.2s;
            }

            .story-girl {
                top: 19px;
                transition: 0.3s;
            }

            .story-renter {
                top: 48px;
                transition: 0.4s;
            }
        }

        .story-boy, .story-girl, .story-renter {
            text-align: center;
            margin-bottom: 5px;
            height: 30px;
            line-height: 30px;
            width: 100%;
            font-size: 14px;
            border: rgba(0, 0, 0, 0.3) 1px solid;
            border-radius: 5px;
            cursor: pointer;

            &:hover {
                border: rgba(255, 255, 255, 0.5) 1px solid;
            }

            .icon {
                margin: 0 20px 0 10px;
            }
        }
    }
}
</style>



