<template>
    <div class="activity-container">
        <div class="activity-header">
            <div class="old-activity" id="old-activity" @click="changeAcivity('oldActivity','old-activity',$event)">过往的活动</div>
            <div class="new-activity selected-item" id="new-activity" @click="changeAcivity('newActivity','new-activity',$event)">将要举办的活动</div>
            <div class="activity-search-btn" id="activity-search-btn" @click="changeAcivity('activitySearch','activity-search-btn',$event)">搜索</div>
        </div>
        <div class="activity-main">
            <keep-alive>
                <components :is='currentCompnent'></components>
            </keep-alive>
        </div>
    </div>
</template>

<script>
import oldActivity from "./oldActivity.vue";
import newActivity from "./newActivity.vue";
import activitySearch from "./activitySearch.vue";
export default {
  components: {
    oldActivity,
    newActivity,
    activitySearch
  },
  data() {
    return {
        currentCompnent: newActivity
    };
  },
  methods: {
    changeAcivity(componentName,id,e){
        let oldActivity = $('#old-activity');
        let newActivity = $('#new-activity');
        let activitySearchBtn = $('#activity-search-btn');
        oldActivity.removeClass('selected-item');
        newActivity.removeClass('selected-item');
        activitySearchBtn.removeClass('selected-item');
        $(`#${id}`).addClass('selected-item');

        this.currentCompnent = componentName;
    }
  }
};
</script>

<style lang="stylus" scoped>
.activity-container {
    width: 100%;
    height: 100%;
    font-size: 14px;

    li {
        list-style-type: none;
    }

    .activity-header {
        display: flex;
        justify-content: space-between;
        height: 40px;
        width: 100%;
        line-height: 40px;
        margin-bottom: 5px;

        .selected-item {
            border-top: 2px red solid;
            border-bottom: solid 1px rgba(0,0,0,0) !important;
            color: red;
        }

        .new-activity, .old-activity, .activity-search-btn {
            height: 100%;
            width: 33.3333%;
            text-align: center;
            border-bottom: solid 1px #ccc;
            cursor pointer
        }

        .old-activity, .new-activity {
            border-right: solid 1px #ccc;
        }
    }

    .activity-main {
        width: 100%;
        height: calc(100% - 40px);
    }
}
</style>



