<template>
  <div class="user-distribution">
    <p class="user-title" @click="toggleSlide()">
      <span v-show="windowWidth < 769">点击查看</span>用户分布
      <span class="icon-reorder" v-show="windowWidth < 769"></span>
    </p>
    <div class="fold-item-ud user-amount" @click="emitEvent({ componentName:'userAmount',dataName:'userAmount', title:'总用户数量'},$event)">
      <span class="icon icon-user"></span>总用户数量
    </div>
    <div class="fold-item-ud user-city" @click="emitEvent({ componentName:'userCity',dataName:'userCity', title:'用户城市分布'},$event)">
      <span class="icon icon-globe"></span>用户城市分布
    </div>
    <div class="fold-item-ud your-city" @click="emitEvent({ componentName:'yourCity',dataName:'yourCity', title:'查看你所在的城市'},$event)">
      <span class="icon icon-map-marker"></span>查看你所在的城市
    </div>
    <div class="video-list-container">
      <p class="title-p">用户评价</p>
      <videoList :videoListData="videoUserListData" :videoPosition="'25%'"></videoList>
    </div>
  </div>
</template>

<script>
import viewDivSlide from "../assets/js/viewdivslide.js";
import videoList from "./videoList";
//导入视频数据（实际上是需要异步请求）
import videoUserListData from "../../data/videoUserListData.js";
//建立数据选择方式
export default {
  data() {
    return {
      videoUserListData,
      windowWidth: window.innerWidth
    };
  },
  components: {
    videoList
  },
  methods: {
    emitEvent(message, e) {
      this.$emit("getData", message);
      viewDivSlide();
    },
    toggleSlide() {
      $(".fold-item-ud").toggleClass("toggle-slide");
    }
  }
};
</script>

<style lang="stylus" scoped>
.user-distribution {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background-color: transparent;
  padding-top: 10px;
  overflow: hidden;

  .user-title {
    text-align: center;
    height: 30px;
    line-height: 30px;
    user-select: none;

    @media only screen and (max-width: 768px) {
      border: rgba(0, 0, 0, 0.3) 1px solid;
      margin-bottom: 10px;
    }
  }

  .user-amount, .user-city, .your-city {
    width: 100%;
    margin-bottom: 5px;
    border: rgba(0, 0, 0, 0.3) 1px solid;
    border-radius: 5px;
    cursor: pointer;
    background-color: transparent;

    &:hover {
      border: rgba(255, 255, 255, 0.5) 1px solid;
    }

    .icon {
      margin: 0 10px;
    }
  }

  .user-amount, .user-city, .your-city {
    height: 30px;
    line-height: 30px;
    font-size: 14px;
  }

  .video-list-container {
    position: relative;
    display: flex;
    flex-direction: column;
    margin-bottom: 5px;
    overflow: hidden;
    width: 100%;
    border: rgba(0, 0, 0, 0.3) 1px solid;
    border-radius: 5px;

    .title-p {
      text-align: center;
    }
  }

  @media only screen and (max-width: 768px) {
    position: relative;

    .fold-item-ud {
      transform: translate3d(0,0,0);
      position: absolute;
      left: 100%;
      background-color: #EDB600;
      z-index: 100;
    }

    .toggle-slide {
      left: 0 !important;
    }

    .user-amount {
      top: 40px;
      transition: 0.2s;
    }

    .user-city {
      top: 69px;
      transition: 0.3s;
    }

    .your-city {
      top: 98px;
      transition: 0.4s;
    }
  }
}
</style>



