<template>
  <main class="main" id="main">
    <div class="swiper">
      <swiper :options='swiperOption' ref="mySwiper">
        <swiper-slide>
          <user-story @getData='getData' :hotArticlesListData='hotArticlesListData'></user-story>
        </swiper-slide>
        <swiper-slide id="prevent-swiper-id">
          <user-distribution @getData='getData'></user-distribution>
        </swiper-slide>
        <swiper-slide>
          <operationTeam @getData='getData'></operationTeam>
        </swiper-slide>
        <swiper-slide>
          <offlineActivity @getData='getData' :activityArticleListData='activityArticleListData'></offlineActivity>
        </swiper-slide>
        <div v-show="screenWidth <= 768" class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
    <div class="view-div"  id="view-div">
      <viewDiv :title='title'>
        <component v-bind:is="viewDivContent" :data='data'></component>
      </viewDiv> 
    </div>
    <div class="article-view-div" id="article-view-div"></div>
  </main>
</template>
<script>
import Vue from "vue";
import userDistribution from "./userDistribution";
import userStory from "./userStory";
import operationTeam from "./operationTeam";
import offlineActivity from "./offlineActivity";
import { swiper, swiperSlide } from "vue-awesome-swiper";
import viewDiv from "./viewDiv";
import bindEntrustEvent from "../assets/js/bindEntrustEvent.js";

import userAmount from "../router/user-amount";
import userCity from "../router/user-city";
import yourCity from "../router/your-city";
import storyList from "../router/story-list";
import activityList from "../router/activityList";

//导入首页要加载的数据
import hotArticlesListData from "../../data/hotArticlesListData.js";
import activityArticleListData from "../../data/activityArticleListData.js";

//导入虚拟数据(应该是在getData方法里获取)
import userAmountData from "../../data/userAmountData.js";
import userCityData from "../../data/userCityData.js";
import yourCityData from "../../data/yourCityData.js";
import boysStoryData from "../../data/boysStoryData.js";
import girlsStoryData from "../../data/girlsStoryData.js";
import zuzhuStoryData from "../../data/zuzhuStoryData.js";
import zukeStoryData from "../../data/zukeStoryData.js";
//建立数据和名称对应关系
let dataObj = {
  userAmountData,
  userCityData,
  yourCityData,
  boysStoryData,
  girlsStoryData,
  zuzhuStoryData,
  zukeStoryData
};

export default {
  data() {
    return {
      swiperOption: {
        autoplay: false,
        allowTouchMove: true,
        init: true,
        // width: '25%',
        // observer:true,
        // observeParents:true,
        noSwiping: true,
        noSwipingClass: "prevent-swiper",
        pagination: {
          el: ".swiper-pagination",
          dynamicBullets: true
        }
      },
      viewDivContent: "",
      data: "",
      title: "",
      screenWidth: "",
      hotArticlesListData,
      activityArticleListData
    };
  },
  methods: {
    getData(message) {
      this.viewDivContent = message.componentName;
      this.data = dataObj[`${message.dataName}Data`];
      this.title = message.title;
    },
    changeScreenWidth() {
      this.screenWidth = window.innerWidth;
    }
  },
  components: {
    userDistribution,
    userStory,
    swiper,
    swiperSlide,
    operationTeam,
    offlineActivity,
    viewDiv,
    userAmount,
    userCity,
    yourCity,
    storyList,
    activityList
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  },
  mounted() {
    let vm = this;
    function changeSwiper() {
      //操作swiper
      if (window.innerWidth > 768) {
        vm.swiper.allowTouchMove = false; //修改参数,禁止滑动

        //根据屏幕宽度修改slide宽度;
        // setTimeout(function() {
        vm.$refs.mySwiper.$children.forEach(c => {
          c.$el.style.width = window.innerWidth * 0.22 + "px";
        });
        centerSlides();
        // }, 0);
      } else if (window.innerWidth <= 768) {
        vm.swiper.allowTouchMove = true; //修改参数

        //修改slide宽度
        $(".swiper-wrapper").width("400%");

        vm.$refs.mySwiper.$children.forEach(c => {
          c.$el.style.width = "25%";
        });
      }
    }

    function centerSlides() {
      let margin =
        ($(".swiper-wrapper").width() -
          $(".swiper-slide")[0].getBoundingClientRect().width * 4) /
        8;
      vm.$refs.mySwiper.$children.forEach((c, i) => {
        switch (i) {
          case 0:
            c.$el.style.marginLeft = `${margin * 2.5}px`;
            break;
          default:
            c.$el.style.marginLeft = `${margin}px`;
            break;
        }
      });
    }
    this.changeScreenWidth();
    // setTimeout(function() {
    //   //$nextTick的时间不够，需要更多时间，所以需要一个timeout
    // }, 400);

    //swiper貌似是在window.onload之后改变slide宽度
    window.onload = function() {
      changeSwiper();
      window.refreshBScroll.forEach(function(c, i) {
        c.refresh();
      });
    };

    //监听hotArticlesList组件触发的preventSwiper事件
    window.preventSwiperVue = new Vue();
    window.preventSwiperVue.$on("preventSwiper", function(el, boo) {
      let preventSwiperEl = $(el);
      if (boo) {
        preventSwiperEl.addClass("prevent-swiper");
      } else {
        preventSwiperEl.removeClass("prevent-swiper");
      }
    });

    //给文章页的容器绑定委托事件，过滤返回按钮的事件
    bindEntrustEvent(
      "click",
      "#article-view-div",
      ".article-return-btn",
      function() {
        $("#article-wrapper").css("left", "0");
        window.bScroll.destroy();
        delete window.bScroll;
      }
    );
  }
};
</script>
<style lang="stylus" scoped>
.main {
  position: relative;
  width: 100%;
  height: calc(100% - 50px);

  .swiper {
    z-index: 10;
    width: 100%;
    height: 100%;

    .swiper-container {
      width: 100%;
      height: 100%;

      .swiper-slide {
        width: 22%;
      }
    }
  }

  .view-div, .article-view-div {
    z-index: 20;
    position: absolute;
    left: 100%;
    top: 0%;
    height: 100%;
    width: 100%;
    background-color: #fff;
    transform: translate3d(0, 0, 0);
  }

  .view-div {
    transform: translate3d(0, 0, 0);
    transition: all 0.3s;
  }

  .article-view-div {
    .article-wrapper {
      position: absolute;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #fff;
      transition: all 0.3s;

      .article-header {
        position: relative;
        height: 40px;
        width: 100%;
        background-color: #d9f4f7;
        border: 1px solid #888;
        text-align: center;
        line-height: 40px;

        .article-return-btn {
          display: block;
          position: absolute;
          left: 0;
          top: 0;
          height: 100%;
          width: 50px;
          cursor: pointer;

          &:hover {
            background-color: #89e3ec;
          }
        }

        .article-title {
          display: block;
          position: absolute;
          left: 50%;
          transform: translate(-50%);
          height: 100%;
          width: calc(100% - 100px);
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }

      .article-main {
        margin: 0 auto;
        width: 90%;
        height: calc(100% - 40px);
        background-color: #fff;

        .article-main-top {
          width: 100%;
          display: flex;
          align-items: center;
          background-color: #fff;

          .article-tags {
            flex-grow: 1;
            background-color: #fff;

            .tag-items {
              float: left;
              margin-top: 5px;
              width: 50%;
              height: 30px;
              line-height: 30px;
              color: #666;
              padding-left: 5px;
              font-size: 14px;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
          }

          .author-head-portrait {
            .head-img {
              display: block;
              float: right;
              border: solid 1px #eee;
              border-radius: 50%;
              height: 100px;
              width: 100px;

              @media only screen and (max-width: 768px) {
                height: 60px;
                width: 60px;
              }
            }
          }
        }

        .article-content {
          min-height: 100px;
          text-align: center;
          border-bottom: solid #ddd 1px;
          background-color: #fff;
        }

        .reward-btn {
          border: solid 1px #000;
          width: 200px;
          height: 30px;
          margin: 20px auto;
          text-align: center;
          line-height: 30px;
          cursor: pointer;
        }

        .article-scroll-wrapper {
          position: relative;
          padding-top: 10px;
          border-top: solid #ddd 1px;
          height: calc(100% - 70px);
          overflow: hidden;
          background-color: #fff;
        }
      }
    }
  }
}
</style>



