<template>
    <div class="user-story">
        <p class="story-title" @click="toggleSlide()">
            <span v-show="windowWidth < 769">点击查看</span>用户出租故事
            <span class="icon-reorder" v-show="windowWidth < 769"></span>
        </p>
        <div class="userClassify">
            <div class="story-class story-boy fold-item-us" @click="emitEvent({ componentName:'storyList',dataName:'boysStory', title:'男生的故事'},$event)">
                男生的故事
                <icon-boy margin='0 10px'></icon-boy>
            </div>
            <div class="story-class story-girl fold-item-us" @click="emitEvent({ componentName:'storyList',dataName:'girlsStory', title:'女生的故事'},$event)">
                女生的故事
                <icon-boy girl='true' margin='0 10px'></icon-boy>
            </div>
            <div class="story-class story-renter fold-item-us" @click="emitEvent({ componentName:'storyList',dataName:'zuzhuStory', title:'租主的故事'},$event)">
                租主的故事
                <span class="icon icon-user"></span>
            </div>
            <div class="story-class story-tenant fold-item-us" @click="emitEvent({ componentName:'storyList',dataName:'zukeStory', title:'租客的故事'},$event)">
                租客的故事
                <span class="icon icon-user"></span>
            </div>
        </div>
        <hotArticlesList :hotArticlesListData='hotArticlesListData'></hotArticlesList>
    </div>
</template>

<script>
import iconBoy from "./iconBoy";
import viewDivSlide from "../assets/js/viewdivslide.js";
import hotArticlesList from "./hotArticlesList";
export default {
  props: ["hotArticlesListData"],
  components: {
    iconBoy,
    hotArticlesList
  },
  data(){
      return {
        windowWidth: window.innerWidth
      }
  },
  methods: {
    emitEvent(message, e) {
      this.$emit("getData", message);
      viewDivSlide();
    },
    toggleSlide() {
      $(".fold-item-us").toggleClass("toggle-slide");
    }
  }
};
</script>

<style lang="stylus" scoped>
.user-story {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    padding-top: 10px;
    overflow: hidden;

    .story-title {
        text-align: center;
        height: 30px;
        line-height: 30px;
        user-select: none;

        @media only screen and (max-width: 768px) {
            border: rgba(0, 0, 0, 0.3) 1px solid;
            margin-bottom: 10px;
        }
    }

    .userClassify {
        width: 100%;

        @media only screen and (max-width: 768px) {
            position: relative;

            .fold-item-us {
                transform: translate3d(0,0,0);
                position: absolute;
                left: 100%;
                background-color: #EDB600;
                z-index: 100;
            }

            .toggle-slide {
                left: 0 !important;
            }

            .story-boy {
                top: -10px;
                transition: 0.2s;
            }

            .story-girl {
                top: 19px;
                transition: 0.3s;
            }

            .story-renter {
                top: 48px;
                transition: 0.4s;
            }

            .story-tenant {
                top: 77px;
                transition: 0.5s;
            }
        }

        .story-boy, .story-girl, .story-renter, .story-tenant {
            text-align: center;
            margin-bottom: 5px;
            height: 30px;
            line-height: 30px;
            width: 100%;
            font-size: 14px;
            border: rgba(0, 0, 0, 0.3) 1px solid;
            border-radius: 5px;
            cursor: pointer;

            &:hover {
                border: rgba(255, 255, 255, 0.5) 1px solid;
            }

            .icon {
                margin: 0 20px 0 10px;
            }
        }
    }
}
</style>



