<template>
    <div class="view-div-container">
        <h3 class="view-div-header">
            <span class="return-btn" @click="slideToRight($event)">返回</span>
            <span class="view-div-title">{{ title }}</span>
        </h3>
        <div class="slot-container">
            <slot></slot>
        </div>
    </div>
</template>

<script>
export default {
  props: ["title"],
  methods: {
    slideToRight(e) {
      $("#view-div").css({
        left: "100%"
      });
    }
  }
};
</script>

<style lang="stylus" scoped>
.view-div-container {
    height: 100%;

    .view-div-header {
        position: relative;
        height: 40px;
        background-color: #d9f4f7;
        font-size: 18px;

        .return-btn {
            display: inline-block;
            padding: 0px 10px;
            height: 100%;
            cursor: pointer;
            line-height: 40px;

            &:hover {
                background-color: #89e3ec;
            }
        }

        .view-div-title {
            display: block;
            position: absolute;
            left: 50%;
            top: 0;
            transform: translateX(-50%);
            line-height: 40px;
        }
    }

    .slot-container {
        height: calc(100% - 40px);
    }
}
</style>




