<template>
  <div class="scroll-container" ref="scrollcontainer">
      <slot></slot>
  </div>
</template>

<script>
import BScroll from "better-scroll";
export default {
  props: {
    scrollConfig: {
      default() {
        return {};
      }
    },
    scrollBoo: {
      default() {
        return true;
      }
    },
    test: String
    // refresh: String
  },
  mounted() {
    // window.scrollArr = [];
    // window.scrollArrNumber = 0;
    this.$nextTick(function() {
      //初始化Bscroll
      this.initScroll();

      //由于swiper的改宽度行为，所以需要在divmain绑定的window.onload之后refresh横向滚动
      let thisScroll = this.scroll;
      window.refreshBScroll.push(thisScroll)
        
      //绑定上拉加载事件
      thisScroll.on("pullingUp", function() {
        console.log("上拉加载ing");
        thisScroll.finishPullUp();
      });
      //绑定下拉刷新事件
      thisScroll.on("pullingDown", function() {
        console.log("下拉刷新ing");
        thisScroll.finishPullDown();
      });
    });
  },
  methods: {
    initScroll() {
      this.scroll = new BScroll(this.$refs.scrollcontainer, this.scrollConfig);
    }
    // refreshScroll() {
    //优雅一点的办法是存下BScroll实例，然后新建Vue实例发送事件，触发实例的refresh刷新
    // window.scrollArr.push({
    //   scroll: this.scroll,
    //   number: window.scrollArrNumber++
    // });

    //low一点的方法是设置定时器刷新
    // let thisScroll = this.scroll;
    // setTimeout(function() {
    //   thisScroll.refresh();
    // }, 500);
    // }
  },
  watch: {
    // 监听数据的变化，调用refresh方法重新计算，保证滚动效果正常
    // data() {
    //   setTimeout(() => {
    //     this.refresh();
    //   }, 0);
    // },
    scrollBoo() {
      if (this.scrollBoo) {
        this.scroll.enable();
      } else {
        this.scroll.disable();
      }
    }
  }
};
</script>

<style lang="stylus" scoped>
.scroll-container {
  position: relative;
  margin-bottom: 5px;
  overflow: hidden;
  width: 100%;
}
</style>



