<template>
    <scroll class="scroll-outside" :scrollBoo="outScrollBoo" :scroll-config="{
                        scrollbar: {
                            fade: true,
                            interactive: false
                        },
                        click: true,
                        mouseWheel: true,
                        pullUpLoad: {
                            threshold: 50
                        },
                        pullDownRefresh: {
                            threshold: 50,
                            stop: 20
                        }
                    }">
        <ul class="activity-list-ul">
            <li class="activity-list-li" v-for="(c,i) in oldActivityListData" :key="i">
                <div class="li-left">
                    <div class="activity-date">日期：{{c.date}}</div>
                    <div class="activity-city">城市：{{c.city}}</div>
                    <div class="activity-address">地点：{{c.address}}</div>
                </div>
                <div class="li-right">
                    <scroll class="scroll-inside" :scrollConfig="{
                                    scrollX: true,
                                    scrollY: false,
                                    mouseWheel: true
                                }" v-on:mouseenter.native="disableOutScroll()" v-on:mouseleave.native="enableOutScroll()">
                        <ul class="activity-img-ul">
                            <li class="activity-img-li" v-for="(item,index) in c.images" :key="index">
                                <img :src="item" alt="活动图片" width="200" height="100">
                            </li>
                        </ul>
                        <div class="activity-description">
                            {{c.description}}
                        </div>
                    </scroll>
                </div>
            </li>
            <div class="empty-div"></div>
        </ul>
    </scroll>
</template>

<script>
import scroll from "../components/scroll.vue";
//导入文章数据（实际上是需要异步请求）
import oldActivityListData from "../../data/oldActivityListData.js";
export default {
  components: {
    scroll
  },
  data() {
    return {
      oldActivityListData,
      outScrollBoo: true
    };
  },
  methods: {
    disableOutScroll() {
      this.outScrollBoo = false;
    },
    enableOutScroll() {
      this.outScrollBoo = true;
    }
  }
};
</script>

<style lang="stylus" scoped>
.scroll-outside {
    height: 100%;
    position: relative;
    overflow: hidden;

    li {
        list-style: none;
    }

    .activity-list-ul {
        width: 100%;
        position: relative;

        .empty-div {
            height: 50px;
        }

        .activity-list-li {
            box-sizing: content-box;
            display: flex;
            width: 100%;
            margin-top: 20px;
            padding: 5px 0;
            border-top: solid 1px #ccc;
            border-bottom: solid 1px #ccc;

            @media only screen and (max-width: 400px) {
                flex-direction: column;
            }

            .li-left {
                width: 200px;
                display: flex;
                flex-direction: column;
                
                @media only screen and (max-width: 400px) {
                    width 100%
                }

                .activity-date, .activity-city, .activity-address {
                    height: 30px;
                    line-height: 30px;
                    text-indent: 1em;
                    flex-grow: 1;
                }

                .activity-date, .activity-city {
                    border-bottom: dashed 1px #ccc;
                }
            }

            .li-right {
                flex-grow: 1;
                height: 100px;
                width: 50%;

                @media only screen and (max-width: 400px) {
                    width 100%
                }

                .scroll-inside {
                    height: 100%;

                    &:hover .activity-description {
                        text-decoration: underline;
                    }

                    .activity-img-ul {
                        display: inline-flex;

                        .activity-img-li {
                            width: 200px;
                            height: 100px;
                            margin-right: 5px;
                        }
                    }

                    .activity-description {
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        height: 30px;
                        width: 100%;
                        line-height: 30px;
                        background-color: rgba(0, 0, 0, 0.2);
                        color: #fff;
                        cursor: pointer;
                        text-indent: 2em;
                    }
                }
            }
        }
    }
}
</style>



