<template>
  <div class="map-container">
    <div class="test">
      <div class="p-container">
        <p>可输入坐标测试 ||</p>
        <p>东经:<input type="number" id="dj" /> ||</p>
        <p>北纬：<input type="number" id="bw" /> ||</p>
        <p><a id="confirm-btn">确定</a> ||</p>
        <p>可在
          <a href="http://api.map.baidu.com/lbsapi/getpoint/index.html" target="_blank">坐标拾取地址</a>
          获取坐标，然后输入后点击确定，坐标点以红点显示
        </p>
      </div>
    </div>
    <div class="canvas-container">
      <canvas ref="canvas"></canvas>
    </div>
  </div>
</template>

<script>
export default {
  props: ["data"],
  mounted() {
    let canvas = this.$refs.canvas;
    let coordinateArr = this.data;
    let img = new Image();
    let activeR;
    let translateX;
    let translateY;
    let boundingRect = canvas.parentNode.getBoundingClientRect();
    let parentWidth = boundingRect.width;
    let parentHeight = boundingRect.height;
    canvas.width = parentWidth;
    canvas.height = parentHeight;
    //中国地图 最西端经度73.569038  最北端纬度53.536101  最东端经度135.199982   最南端纬度18.190173
    const WESTPOINT = 73.569038;
    const NORTHPOINT = 53.536101;
    const EASTPOINT = 135.199982;
    const SOUTHPOINT = 18.190173;
    //canvas绘制的地图宽高为
    let mapWidth;
    let mapHeight;
    //设置偏差修正值
    let xChange;
    let yChange;
    if (window.innerWidth >= 1024) {
      img.src = "src/assets/big.jpg";
      activeR = 4;
      mapWidth = 829;
      mapHeight = 610;
      xChange = 1;
      yChange = 31;
    } else if (window.innerWidth < 1024 && window.innerWidth >= 768) {
      img.src = "src/assets/middle.jpg";
      activeR = 3;
      mapWidth = 574;
      mapHeight = 423;
      xChange = 1;
      yChange = 22;
    } else if (window.innerWidth < 768) {
      img.src = "src/assets/small.jpg";
      activeR = 2;
      mapWidth = 308;
      mapHeight = 227;
      xChange = 0;
      yChange = 10;
    }
    //左上角0,0坐标为 东经73.569038  北纬53.536101
    const xRange = EASTPOINT - WESTPOINT;
    const yRange = NORTHPOINT - SOUTHPOINT;
    //度数和像素比率为 px / deg
    let widthPxRate = mapWidth / xRange;
    let heightPxRate = mapHeight / yRange;
    //定义px坐标点和地理坐标点转换方法
    function transformPxPoint(
      coordinatePoint = {
        e: 116.392196,
        n: 40.016244
      }
    ) {
      //输入地理坐标点，转换成像素坐标点  上海坐标121.4664,31.257049  三亚坐标109.508157,18.269289
      let xyObj = {};
      if (coordinatePoint.n < 31.257049) {
        //因为地图不标准，上海以南和上海以北的y计算方式不同
        xyObj.x = (coordinatePoint.e - WESTPOINT) * widthPxRate + xChange;
        xyObj.y =
          (NORTHPOINT - coordinatePoint.n) * heightPxRate +
          yChange * (1 - (31.257049 - coordinatePoint.n) / 13);
      } else {
        xyObj.x = (coordinatePoint.e - WESTPOINT) * widthPxRate + xChange;
        xyObj.y = (NORTHPOINT - coordinatePoint.n) * heightPxRate + yChange;
      }
      return xyObj;
    };
    //将数据中的坐标点转换成px坐标点
    let xyArr = [];
    for (let i = 0; i < coordinateArr.length; i++) {
      let xyObj = transformPxPoint(coordinateArr[i]);
      xyArr.push(xyObj);
    }
    //定义坐标点绘制方法
    let ctx = canvas.getContext("2d");
    function fillCircle(centerX, centerY, r, color) {
      ctx.beginPath();
      ctx.arc(centerX, centerY, r, 0, 2 * Math.PI);
      ctx.fillStyle = color;
      ctx.closePath();
      ctx.fill();
    };
    img.onload = function() {
      translateX = (canvas.width - img.width) / 2;
      translateY = 0;
      ctx.translate(translateX, translateY);
      ctx.drawImage(img, 0, 0);
      //绘制坐标点
      for (let j = 0; j < xyArr.length; j++) {
        fillCircle(xyArr[j].x, xyArr[j].y, activeR, "blue");
      }
    };
    //测试坐标
    let confirmBtn = $("#confirm-btn");
    confirmBtn.on("click", function() {
      let dj = $("#dj").val();
      let bw = $("#bw").val();
      let xyObj = transformPxPoint({
        e: dj,
        n: bw
      });
      let a = 1;
      setInterval(function() {
        ctx.clearRect(xyObj.x - 5, xyObj.y - 5, 10, 10); //清空矩形必须要先于绘制执行
        fillCircle(xyObj.x, xyObj.y, 4, `rgba(255,0,0,${a})`);
        if (a > 0) {
          a = a - 0.2;
        } else {
          a = 1;
        }
      }, 100);
    });
  }
};
</script>

<style lang="stylus" scoped>
.map-container {
  width: 100%;
  height: 100%;

  .test {
    .p-container {
      p {
        @media only screen and (min-width: 769px) {
          display: inline;
        }

        @media only screen and (max-width: 768px) {
          margin: 2px 0;
        }
      }
    }

    #confirm-btn {
      display: inline-block;
      padding: 0px 5px;
      background: red;
      cursor: pointer;
    }
  }

  .canvas-container {
    width: 100%;
    height: 100%;
  }
}
</style>



