<template>
    <div class="team-said">
        <p class="team-said-title" @click="toggleSlide()">
            <span v-show="windowWidth < 769">点击查看</span>租我么团队寄语
            <span class="icon-reorder" v-show="windowWidth < 769"></span>
        </p>
        <div class="what-to-do fold-item-ot" @click="getArticleData('租我么能用来干什么',$event)">
            <span class="icon icon-question-sign"></span>
            租我么能用来干什么
        </div>
        <div class="app-safety fold-item-ot"  @click="getArticleData('租我么的安全保障',$event)">
            <span class="icon icon-ok-circle"></span>
            租我么的安全保障
        </div>
        <div class="video-list-container">
            <p class="title-p">CEO的视频介绍</p>
            <videoList :videoListData="videoCeoListData"  :videoPosition="'50%'"></videoList>
        </div>
    </div>
</template>

<script>
import openArticle from "../assets/js/openArticle.js";
import videoList from "./videoList";
//导入视频数据（实际上是需要异步请求）
import videoCeoListData from "../../data/videoCeoListData.js";

//导入文章数据（实际上是需要异步请求）
import { whatToDo } from "../../data/operationTeamArticle.js";
export default {
  data() {
    return {
      videoCeoListData,
      windowWidth: window.innerWidth
    };
  },
  components: {
    videoList
  },
  methods: {
    getArticleData(message, e) {
      openArticle(whatToDo);
    },
    toggleSlide() {
      $(".fold-item-ot").toggleClass("toggle-slide");
    }
  }
};
</script>

<style lang="stylus" scoped>
.team-said {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    padding-top: 10px;
    background-color: transparent;
    overflow: hidden;

    .team-said-title {
        text-align: center;
        height: 30px;
        line-height: 30px;
        user-select: none;

        @media only screen and (max-width: 768px) {
            border: rgba(0, 0, 0, 0.3) 1px solid;
            margin-bottom: 10px;
        }
    }

    @media only screen and (max-width: 768px) {
        position: relative;

        .fold-item-ot {
            transform: translate3d(0,0,0);
            position: absolute;
            left: 100%;
            background-color: #EDB600 !important;
            z-index: 100;
        }

        .toggle-slide {
            left: 0 !important;
        }

        .what-to-do {
            top: 40px;
            transition: 0.2s;
        }

        .app-safety {
            top: 69px;
            transition: 0.3s;
        }
    }

    .what-to-do, .app-safety, .ceo-video {
        width: 100%;
        margin-bottom: 5px;
        border: rgba(0, 0, 0, 0.3) 1px solid;
        border-radius: 5px;
        cursor: pointer;
        background-color: transparent;

        &:hover {
            border: rgba(255, 255, 255, 0.5) 1px solid;
        }

        .icon {
            margin: 0 10px;
        }
    }

    .what-to-do, .app-safety {
        height: 30px;
        line-height: 30px;
        font-size: 14px;
    }

    .ceo-video {
        flex-grow: 1;
    }

    .video-list-container {
        position: relative;
        display: flex;
        flex-direction: column;
        margin-bottom: 5px;
        overflow: hidden;
        width: 100%;
        border: rgba(0, 0, 0, 0.3) 1px solid;
        border-radius: 5px;

        .title-p {
            text-align: center;
        }
    }
}
</style>



