<template>
    <div class="search-container">
        <div class="search-header">
            <input class="search-activity-input" id="search-activity-input" placeholder="搜索功能待开发" type="text">
            <span class="search-btn icon-search" @click="searchActivity()"></span>
        </div>
        <div class="search-main">
            搜索结果展示区
        </div>
    </div>
</template>

<script>
export default {
  methods: {
    searchActivity() {
      let inputValue = $("#search-activity-input").val();
    }
  }
};
</script>

<style lang="stylus" scoped>
.search-container {
    width: 100%;
    height: 100%;

    .search-header {
        display: flex;
        justify-content: center;
        width: 100%;
        height: 40px;
        line-height: 40px;
        margin-top: 10px;

        .search-activity-input {
            width: 80%;
            height: 100%;
            outline: none;
            padding: 0 10px;
        }

        .search-btn {
            height: 100%;
            width: 50px;
            line-height: 35px;
            font-size: 20px;
            border: solid 1px #ccc;
            color: #666;
            text-align: center;
            cursor: pointer;

            &:hover {
                color: #000;
            }
        }
    }

    .search-main {
        width: 100%;
        height: calc(100% - 40px);
        margin-top: 10px;
        border-top: dashed 1px #ccc;
    }
}
</style>



