export default function (ad = {
    title:'',
    author: '',
    city: '',
    gender: '',
    memeNumber: '',
    headImageSrc: '',
    theText: '文章未找到'
},scopedAttrName) {
    let template =
    `<div ${scopedAttrName} class="article-wrapper" id="article-wrapper">
      <div ${scopedAttrName} class="article-header">
        <span ${scopedAttrName} class="article-return-btn" id="article-return-btn">返回</span>
        <span ${scopedAttrName} class="article-title">${ad.title}</span>
      </div>
      <div ${scopedAttrName} class="article-main">
        <div ${scopedAttrName} class="article-main-top">
          <div ${scopedAttrName} class="article-tags">
            <div ${scopedAttrName} class="tag-items">作者：${ad.author}</div>
            <div ${scopedAttrName} class="tag-items">性别：${ad.gender}</div>
            <div ${scopedAttrName} class="tag-items">城市：${ad.city}</div>
            <div ${scopedAttrName} class="tag-items">么么号：${ad.memeNumber}</div>
          </div>
          <div ${scopedAttrName} class="author-head-portrait">
            <img ${scopedAttrName} class="head-img" src="${ad.headImageSrc}" alt="用户头像" width="100" height="100">
          </div>
        </div>
        <div ${scopedAttrName} class="article-scroll-wrapper">
          <div ${scopedAttrName} class="article-scroll">
            <div ${scopedAttrName} class="article-content">
              ${ad.theText}
            </div>
            <div ${scopedAttrName} class="reward-btn">
              喜欢作者？点击可打赏
            </div>
            <div style="height:50px"></div>
          </div>
        </div>
      </div>
    </div>`

  return template;
}


